* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
  padding: 0;

  font-family: "Poppins", sans-serif;
}

body {
  background-color: #F0F0F0;
}

nav {
  background-color: #F0F0F0;
  height: 100px;
  display: flex;
  justify-content: space-between;
  position: fixed;
  right: 0;
  left: 0;
  padding-top: 50px;
}

.site-logo {
  margin-left: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
  margin-right: 10rem;
}

nav ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
}

nav ul li a {
  font-size: 18px;
  font-weight: 900;
  align-items: center;
  text-decoration: none;
  color: #0D0D0D;
  transition: all .2s;
  text-transform: uppercase;
  text-shadow: -1px 1px #0d0d0d62;

  position: relative;
}

.nav ul li a:before {
  content: '';
  height: 2px;
  width: 0;
  top: 26px;
  border-radius: 8px;
  position: absolute;
  transition: all .2s linear;
  background-color: #0D0D0D;
  text-shadow: -1px 1px #0d0d0d62;
}

.nav ul li a:hover {
  color: #6b6b6b;
}

.nav ul li a:hover::before {
  width: 100%;
  background-color: #6b6b6b;
}

.contents .home,
.contents .about,
.contents .contact {
  height: 100vh;
  font-size: 80px;
  color: #F0F0F0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home,
.about,
.contact {
  background-color: #F0F0F0;
}

h1 {
  color: #F0F0F0;
  font-weight: 900;
  -webkit-text-stroke: 3px #0D0D0D;
  position: relative;
  text-transform: uppercase;
  text-shadow: -6px 6px #0d0d0d62;
}

.home h1::before,
.about h1::before,
.contact h1::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  color: #0D0D0D;
  border-right: 3px solid #0D0D0D;
  overflow: hidden;
  animation:  animate 4s linear infinite;
}

@keyframes animate {
  0%,10%,100% {
    width: 0;
  }
  70%,90% {
    width: 100%;
  }
}